import React from 'react';

const Technical = (props) => {
    if (props.tech.data) {
      if (props.tech.data.length === 0) {
        return <p>      </p>;
      } else {
        return (
          <table>
            <tbody>
              {props.tech.data.map(data => (
                <tr key={data.slug}>
                  <td>{data.title}</td>
                  <td>{data.value}</td>
                </tr>
              ))}
            </tbody>
          </table>
        );
      }
    } else {
      return <p>      </p>;
    }
  }
  export default Technical;